import { Directive, ViewContainerRef, TemplateRef, Input, Renderer2, ElementRef, Output, EventEmitter } from '@angular/core';
import { Observable, fromEvent } from 'rxjs';
import { map } from 'rxjs/operators';
import { ActivatedRoute, Router } from "@angular/router";

@Directive({ selector: '[scrollEnd]' })
export class ScrollEndDirective {

  container: any;

  @Input() scrollerEnabled: boolean = true;
  @Input() scrollingParent: string;
  triggered: boolean = false;

  @Output() scrollEndReached: EventEmitter<any> = new EventEmitter();

  constructor(private el: ElementRef, private render: Renderer2, private router: Router) {

  }

  ngAfterViewInit(): void {

    if (!this.scrollerEnabled) return;
  
    if (this.scrollingParent == "") {
 
 
      let _window : Window = this.container = window;

      const scroll$ = fromEvent(_window.document, 'scroll').pipe(map(() => _window));
 
      scroll$.subscribe(element => { 
        this.onWindowScroll(element);
      });

    } else {

 
      this.container =  document.querySelector(this.scrollingParent);
 
      const scroll$ = fromEvent(this.container, 'scroll').pipe(map(() => this.container));
 
      scroll$.subscribe(element => { 
        this.onWindowScroll(element);
      });

    }
    this.router.events.subscribe(url => {
      let sideBar = this.el.nativeElement;
      if (this.container instanceof Window) {

        this.container.document.body.scrollTop = 0;

      } else if (this.container instanceof Element) {

        this.container.scrollTop = 0;
      }
      setTimeout(() => {
        this.onWindowScroll(this.container);
      }, 0)
    });

  }

  ngOnInit() {


  }


  getOffset(el: any) {
    const rect = el.getBoundingClientRect();
    return {
      left: rect.left + window.scrollX,
      top: rect.top + window.scrollY
    };
  }

  onWindowScroll(element: any) {

    let content = this.el.nativeElement;
    var totalHeight = content.clientHeight;
    var scrolledHeight = 0;
    if (this.container instanceof Window) {
      scrolledHeight = this.container.document.body.scrollTop + this.container.document.body.clientHeight;
    } else {
      scrolledHeight = this.container.scrollTop + this.container.clientHeight;
    }
 
    if (scrolledHeight >= totalHeight) {

      if (!this.triggered) {
        this.triggered = true;
        setTimeout(() => {
          this.triggered = false;
        }, 1000);
        this.scrollEndReached.emit();
      }
    }


  }
}