import { KeyValue } from '@angular/common';
import { Category } from './category';
import { Product, ProductVariant } from './product';

export class BaseFiler {

    public SortBy!: string;
    public SortOrder!: string;
    public SearchKey!: string;
    public PageNumber!: number;
    public PageSize!: number;
}

export class MenuItems {
    orderNumber!: number;
    text!: string;
    url!: string;
    icon!: string;
    iconPosition?: string = "left";
    isActive!: boolean;
    visible?: boolean = true;
    isDefault?: boolean = false;
}

export class SideNavigation {
    orderNumber!: number;
    name!: string;
    menuItems!: MenuItems[];
    visible?: boolean = false;
}

export class ResponseObject<T> {
    IsSuccess?: boolean = false;
    Result!: Result;
    Message!: string;
    Data?: T;
}

export class ModalRequest {
    modalType: ModalType;
    open: boolean;
    data: Product | ProductVariant | Category | null;
}

export enum ModalType {
    ProductOverview = 0,
    ProductVariant = 1,
    TimingSelection = 2
}

export enum Result {
    ValidationError = 0,
    Success = 1,
    Failure = 2,
    Error = 3,
    NotFound = 4, 
    Unauthorized = 5
}

export enum ProductDivision {
    None = -1,
    Grocery = 0,
    HygineAndCleaning = 1,
    PersonalCare = 2,
    BabiesAndKids = 3,
    Stationeries = 4,
    SportsAndFitnes = 5,
    HouseHoldItems = 6
}

export enum OrderStatus {
    All = 0,
    Placed = 1,
    Confirmed = 2,
    Packed = 3,
    Dispatched = 4,
    Delivered = 5,
    Cancelled = 6,
    Rejected = 7,
    Pending = 8
}


export enum PaymentType {
    CashOnDelivery = 1,
    OnlinePayment = 2,
}

