import { AfterViewChecked, Component, Injector, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ResponseObject } from 'src/app/models/common';
import { ApiRequestService } from 'src/app/services/api-services/api-request.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { environment } from 'src/environments/environment';
import { AuthenticationService } from '../_services/authentication.service';

@Component({
  selector: 'app-two-factor',
  templateUrl: './two-factor.component.html',
  styleUrls: ['./two-factor.component.scss']
})
export class TwoFactorComponent extends BaseComponent implements OnInit {

  modalId: string = 'otp-modal';

  get phoneNumber(): string {
    return this.authService.currentUserValue?.userName;
  }

  get isRequiredOTP(): boolean {
    return this.authService.currentUserValue?.requireOTP === "True";
  }

  formInstance: FormGroup = this.fb.group({
    OtpCode: ['', [Validators.required]]
  })

  constructor(protected injector: Injector,
    private authService: AuthenticationService) {
    super(injector);
  }

  ngOnInit(): void {
    this.loadSpinner = "";
    this.showModal(this.modalId, () => {
      this.modalClose()
    });
  }

  modalClose() {
    if (this.router != null) {
      this.router.navigate([{ outlets: { action: [], primary: [] } }])
    }
  }

  submitOtp() {
    let otp = this.formInstance.get("OtpCode")?.value;
    this.showFullSpinner();
    this.apiRequestService.get(environment.apiUrl + '/auth/verify-otp/' + otp).subscribe((res: ResponseObject<any>) => {
      this.formInstance.reset();
      this.hideFullSpinner();
      this.authService.updateRequireOTP(false);
      this.modalClose()
    }, error => {
      this.modalClose()
    })
  }

}
