import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { environment } from 'src/environments/environment';
import { ModalRequest } from '../models/common';

@Injectable({
    providedIn: 'root'
})
export class UtilitiesService {

    private _openCloseModalDialog: Subject<ModalRequest> = new Subject<ModalRequest>();
    public dialogRequest: Observable<any> = this._openCloseModalDialog.asObservable();

    constructor() {
    }

    closeModalDialog(request: ModalRequest) {
        request.open = false;
        this._openCloseModalDialog.next(request);
    }

    openModalDialog(request: ModalRequest) {
        request.open = true;
        this._openCloseModalDialog.next(request);
    }
}
