import { Injectable } from '@angular/core';
import { Router, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { AuthenticationService } from '../_services/authentication.service';

@Injectable({
  providedIn: 'root'
})
export class RoleGuardService {

  constructor(private _authService: AuthenticationService, private _router: Router) { }

  canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {

    const currentUser = this._authService.currentUserValue;

    let res: boolean = false;

    let isVerifiedUser = this._authService.isUserLoggedIn;

    if (currentUser != null && next.data.roles === undefined) {
      res = true;
    }
    else if (isVerifiedUser) {

      let userRoles = (currentUser.roles ?? "").split(",");

      res = next.data.roles && next.data.roles.some((ai: any) => userRoles.includes(ai));
    }

    if (res == false) {

      this._router.navigate(['/login']);
    }

    return res;
  }
}