import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';


import { NotFoundComponent } from './not-found/not-found.component';
import { ActivityComponent } from './activity/activity.component';
import { LayoutComponent } from './layout/layout.component';
import { environment } from 'src/environments/environment.prod';
import { LoadSpinnerComponent } from './load-spinner/load-spinner.component';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { LoginComponent } from '../auth/login/login.component';
import { LogoutComponent } from '../auth/logout/logout.component';
import { ScrollEndDirective } from './directives/scroll-end';
import { BannerImageCarouselComponent } from './banner-image-carousel/banner-image-carousel.component';
import { BottomNavModule } from './ngx-bottom-nav/bottom-nav.module';
import { HttpClientModule } from '@angular/common/http';
import { NgxSpinnerModule } from 'ngx-spinner';
import { TwoFactorComponent } from '../auth/two-factor/two-factor.component';
import { RemoveCommaPipe } from './pipes/decimal-pipes';
import { NotifierModule, NotifierOptions } from 'angular-notifier';


const customNotifierOptions: NotifierOptions = {
    position: {
      horizontal: {
        position: 'middle',
        distance: 12
      },
      vertical: {
        position: 'bottom',
        distance: 30,
        gap: 30
      }
    },
    theme: 'material',
    behaviour: {
      autoHide: 500000,
      onClick: 'hide',
      onMouseover: 'pauseAutoHide',
      showDismissButton: true,
      stacking: 4
    },
    animations: {
      enabled: true,
      show: {
        preset: 'slide',
        speed: 300,
        easing: 'ease'
      },
      hide: {
        preset: 'fade',
        speed: 300,
        easing: 'ease',
        offset: 50
      },
      shift: {
        speed: 300,
        easing: 'ease'
      },
      overlap: 150
    }
  };

@NgModule({
    declarations: [
        NotFoundComponent,
        ActivityComponent,
        LayoutComponent,
        LoadSpinnerComponent,
        LoginComponent,
        TwoFactorComponent,
        LogoutComponent,
        ActivityComponent,
        ScrollEndDirective,
        RemoveCommaPipe,
        BannerImageCarouselComponent
    ],
    imports: [
        CommonModule,
        FormsModule,
        FormsModule,
        ReactiveFormsModule,
        ReactiveFormsModule,
        BottomNavModule,
        //NgxUsefulSwiperModule,
        HttpClientModule,
        NgxSpinnerModule,
        NotifierModule.withConfig(customNotifierOptions)
        // NgQrScannerModule,
        // QRCodeModule,
    ],
    exports: [
        ActivityComponent,
        HttpClientModule,
        CommonModule,
        LayoutComponent,
        BannerImageCarouselComponent,
        LoginComponent,
        RemoveCommaPipe,
        TwoFactorComponent,
        // NgQrScannerModule,
        // QRCodeModule,
        ActivityComponent,
        ReactiveFormsModule,
        BottomNavModule,
        LoadSpinnerComponent,
        FormsModule,
        ReactiveFormsModule,
        ScrollEndDirective,
        //NgxUsefulSwiperModule,
        NotifierModule,
        NgxSpinnerModule,
    ]
})
export class SharedModule { }
