import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { BehaviorSubject, Observable, observable } from 'rxjs';
import { map, min } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { User } from '../_models/user';


@Injectable({ providedIn: 'root' })
export class AuthenticationService {

    private currentUserSubject: BehaviorSubject<any>;
    public currentUser: Observable<any>;

    constructor(private http: HttpClient) {

        let obj = localStorage.getItem('currentUser');

        this.currentUserSubject = new BehaviorSubject<any | null>(obj != null ? JSON.parse(obj) : null);

        this.currentUser = this.currentUserSubject.asObservable();
    }

    public get currentUserValue(): any {

        return this.currentUserSubject.value;
    }

    public get isUserLoggedIn(): boolean {
       return this.currentUserValue != null && this.currentUserValue.requireOTP == 'False';
    }

    login(username: string, password: string): Observable<any> {

        const myheader = new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded');

        let body = new HttpParams();

        body = body.set('UserName', username);
        body = body.set('Password', password);
        body = body.set('grant_type', "password");

        return this.http.post<any>(`${environment.appAuthUrl}`, body, {
            headers: myheader
        }).pipe(map(user => {

            localStorage.setItem('currentUser', JSON.stringify(user));

            this.currentUserSubject.next(user);

            return user;
        }));
    }

    updateRequireOTP(res: boolean) {
        let user = this.currentUserValue;
        user.requireOTP = res ? "True" : "False";
        localStorage.setItem('currentUser', JSON.stringify(user));
        this.currentUserSubject.next(user);
    } 

    signup(fullName: string, phoneNumber: string, username: string, password: string, confirm: string): Observable<any> {

        return this.http.post<any>(`${environment.apiUrl}auth/register`, {
            "Aadhar": "", "Address": "",
            "FullName": fullName, "PhoneNumber": phoneNumber,
            "Email": username, "Password": password, "ConfirmPassword": confirm
        })
            .pipe(map(user => {

                localStorage.setItem('currentUser', JSON.stringify(user));

                this.currentUserSubject.next(user);

                return user;
            }));
    }

    logout(): Observable<any> {

        let url = environment.apiUrl;
        
        return this.http.post<any>(`${url}auth/logout`, null).pipe(map(user => {
  
            localStorage.removeItem('currentUser');

            this.currentUserSubject.next(null);

            return user;
        }))
    }
}
