import { FormBuilder, FormGroup } from "@angular/forms";
import { OnDestroy, OnInit, Injector, Directive, Component, inject } from "@angular/core";
import { ErrorInterceptor } from "../auth/_helpers/error.interceptor";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable, of, Subscription } from "rxjs";
import { map, catchError, filter } from "rxjs/operators";
import { environment } from "src/environments/environment";
import { ErrorInterceptorService } from "./services/error-interceptor.service";
import { ActivatedRoute, NavigationEnd, Router, RoutesRecognized } from "@angular/router";
import { NgxSpinnerService } from "ngx-spinner";
import { ResponseObject, Result } from "../models/common";
import { ApiRequestService } from "../services/api-services/api-request.service";
import { UtilitiesService } from "../services/utilities.service";

declare var UIkit: any;

@Component({
    template: ''
})
export class BaseComponent implements OnInit, OnDestroy {

    public messageShown: boolean = false;

    protected subscription!: Subscription;

    protected hasBottomNavigation: boolean = true;

    protected baseUrl: string = environment.apiUrl;

    public get returnUrl(): any {
        let routes = this.router.url.split("return");
        if (routes.length > 1) { return { return: routes[1].replace('/', '') } }
        else {
            return {};
        }
    }

    loading: boolean = false;

    loadSpinner: string = "loadSpinner";

    spinnerOptions: any = {
        type: "ball-spin-clockwise", //"square-jelly-box",
        size: "small",
        bdColor: "rgba(0,0,0,0)",
        color: "#329225",
        fullScreen: false
    };

    protected fb!: FormBuilder;
    protected router: Router;
    protected errorIntrcptr: ErrorInterceptorService;
    protected spinner: NgxSpinnerService;
    protected route: ActivatedRoute;
    protected apiRequestService: ApiRequestService;
    protected utilities: UtilitiesService;

    constructor(protected injector: Injector) {

        this.fb = injector.get(FormBuilder);
        this.router = injector.get(Router);
        this.errorIntrcptr = injector.get(ErrorInterceptorService);
        this.spinner = injector.get(NgxSpinnerService);
        this.route = injector.get(ActivatedRoute);
        this.apiRequestService = injector.get(ApiRequestService);
        this.utilities = injector.get(UtilitiesService);

        this.subscription = this.apiRequestService.onAsyncStatus$.subscribe(res => {
            if (res) {
                this.showSpinner();
            }
            else {
                this.hideSpinner();
            }
        });
    }

    resetForm(form: FormGroup) {
        form.reset();
        form.markAsPristine();
        form.markAsUntouched();
    }

    ngOnInit() {
    }

    showModal(name: string, callBack?: Function) {
        if (name.charAt(0) != "#") {
            name = "#" + name;
        }
        setTimeout(() => {
            let modal = UIkit.modal(name);
            if (modal) {
                UIkit.modal(name).show();
                UIkit.util.on(name, 'hidden', () => {
                    if (callBack) callBack();
                });
            }
        }, 0);
    }

    hideModal(name: string, domReUse: boolean = false) {
        if (name.charAt(0) != "#") {
            name = "#" + name;
        }
        let _name = name.split("#")[1];
        setTimeout(() => {
            let modal = UIkit.modal(name);
            if (modal) modal.hide();
            if (!domReUse) {
                setTimeout(() => {
                    let ele = document.getElementById(_name);
                    ele?.remove();
                }, 100);
            }
        }, 0);
    }

    ngOnDestroy() {
        if (this.subscription) {
            console.log("UnSubscribing the Interceptor")
            this.subscription.unsubscribe();
        }
        this.router = <any>null;
        this.errorIntrcptr = <any>null;
    }

    showSpinner() {
        if (this.loadSpinner != "") {
            this.spinner.show(this.loadSpinner, this.spinnerOptions);
        } else {
            this.showFullSpinner();
        }
    }

    hideSpinner() {
        if (this.loadSpinner != "") {
            this.spinner.hide(this.loadSpinner);
        } else {
            this.hideFullSpinner();
        }
    }

    showFullSpinner() {
        this.spinner.show();
    }

    hideFullSpinner() {
        this.spinner.hide();
    }


    getDay(dayId: number): any {

        let date: Date = (new Date());
        let ind = (date.getDay() < 6 ? date.getDay() + 1 : 0);
        while (ind != dayId) {
            date = this.addDays(date, 1);
            ind = (date.getDay() < 6 ? date.getDay() + 1 : 0);
        }

        return date;
    }

    tConvert(_hours: string, _minutes: string) {
         if(_hours == null || _minutes == null) {
            return "";
        }
        // Check correct time format and split into components
        _hours = parseInt(_hours) < 10 ? "0" + _hours : _hours;
        _minutes = parseInt(_minutes) < 10 ? "0" + _minutes : _minutes;

        let time: any = (_hours + ":" + _minutes).toString().match(/^([01]\d|2[0-3])(:)([0-5]\d)(:[0-5]\d)?$/) || _hours + ":" + _minutes;

        if (time.length > 1) { // If time format correct
            time = time.slice(1);  // Remove full string match value
            time[5] = +time[0] < 12 ? 'AM' : 'PM'; // Set AM/PM
            time[0] = +time[0] % 12 || 12; // Adjust hours
        }
        return time.join(':').replace(":::", ":").replace(":::", " "); // return adjusted time or original string
    }

    addDays(theDate: Date, days: number) {
        return new Date(theDate.getTime() + days * 24 * 60 * 60 * 1000);
    }

}