import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor, HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { Observable, throwError, Subject, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { AuthenticationService } from '../_services/authentication.service';
import { Router } from '@angular/router';
import { ErrorInterceptorService } from 'src/app/shared/services/error-interceptor.service';
import { Result } from 'src/app/models/common';
import { environment } from 'src/environments/environment';


@Injectable()
export class ErrorInterceptor implements HttpInterceptor {

    constructor(
        private authenticationService: AuthenticationService,
        private _router: Router,
        private errorIntrcptr: ErrorInterceptorService) { }

    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        return next.handle(request).pipe(catchError(this.handleError<any>()))
    }

    private handleError<T>(operation = 'operation', result?: T) {

        return (response: any): Observable<T> => {


            if (response.status === 401) {
   
                this.errorIntrcptr.httpErrorOccurred.next({
                    Message: "Please login to continue..", Result: Result.Unauthorized,
                    Data: {
                        callback: () => {
                            if (environment.isDesktop) {
                                this._router.navigateByUrl("");
                                this.authenticationService.logout().subscribe(() => {
                                    this._router.navigateByUrl("/login");
                                });
                            } else {
                                this._router.navigateByUrl("");
                                this.authenticationService.logout().subscribe(() => {
                                    this._router.navigateByUrl("/login");
                                });
                            }
                        }
                    }
                });

            }
            else if (response.status == 400) {
                let msg = response.error.Message;
                this.errorIntrcptr.httpErrorOccurred.next({ Message: msg, Result: Result.Failure, Data: response });
            }
            else if (response.status == 500) {
                this.errorIntrcptr.httpErrorOccurred.next({ Message: "Error processing your request", Result: Result.Error, Data: response });
            }
            else if (response.status == 404) {
                this.errorIntrcptr.httpErrorOccurred.next({ Message: "Requested service could not be found", Result: Result.NotFound, Data: response });
            }
            else if (response.status == 0) {
                this.errorIntrcptr.httpErrorOccurred.next({ Message: "Error processing your request", Result: Result.Failure, Data: response });
                this._router.navigateByUrl("/server-down");
            }
            else {
                this.errorIntrcptr.httpErrorOccurred.next({ Message: "Unknown Error", Result: Result.Error, Data: response });
            }

            if (!environment.production) {
                console.log(`${operation} failed: ${response.message}`);
            }
            return of(result as T);
        };
    }
}