import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { environment } from '../../../environments/environment';
import { ApiRequestService } from './api-request.service';
import { ResponseObject } from 'src/app/models/common';
import { UserService } from 'src/app/auth/_services/user.service';

@Injectable({
    providedIn: 'root'
})
export class NotificationMessageService {

    interval: any;

    private notificationFetch: Subject<any[]> = new Subject<any[]>();
    public onNotificationFetched$: Observable<any[]> = this.notificationFetch.asObservable();

    private save_notification_token_url: string = environment.apiUrl + "app-notification/save-system-key";
    private save_pushed_notification_url: string = environment.apiUrl + "app-notification/save-pushed-notification";
    private fetch_notification: string = environment.apiUrl + "app-notification/fetch-notification";

    constructor(protected apiService: ApiRequestService, protected userService: UserService) {

    }

    public saveNotificationToken(notificaitonTokenObj: object): Observable<any> {
        console.log('saving token')
        return this.apiService.post(this.save_notification_token_url, notificaitonTokenObj);
    }

    public savePushedNotificationUrl(pushedNtificationObj: any): Observable<any> {
        return this.apiService.post(this.save_pushed_notification_url, pushedNtificationObj);
    }

    public startFetchningNotifications() {
        this.interval = setInterval(() => {
            this.apiService.get(this.fetch_notification).subscribe((res: ResponseObject<[]>) => {
                if (res != null && res.Data != null && res.Data.length > 0) {
                    this.notificationFetch.next(res.Data);
                }
            });
        }, 10000);
    }

}