import { Injectable, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { SharedModule } from './shared/shared.module';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { JwtInterceptor } from './auth/_helpers/jwt.interceptor';
import { ErrorInterceptor } from './auth/_helpers/error.interceptor';
import { DefaultUrlSerializer, UrlSerializer, UrlTree } from '@angular/router';
import { environment } from '../environments/environment';
import { AngularFireModule, FirebaseApp } from '@angular/fire/compat';
import { AngularFireMessagingModule } from '@angular/fire/compat/messaging';
import { AsyncPipe } from '@angular/common';
import { FireCloudMessagingService } from './services/fiire-cloud-messaging-service';


export class MyUrlSerializer extends DefaultUrlSerializer implements UrlSerializer {
  serialize(tree: UrlTree): string {
    let url = super.serialize(tree).replace(/\(|\)|\w+-\w+:/g, '');
    if (url.indexOf('action:') > -1) {
      url = (url.split('action:')[0] + '/' + url.split('action:')[1]).replace('//', '/');
    }
    return url;
  }
}

@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    SharedModule,
    AngularFireMessagingModule,
    AngularFireModule.initializeApp(environment.firebaseConfig)    
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
    { provide: UrlSerializer, useClass: MyUrlSerializer },
    FireCloudMessagingService, AsyncPipe
  ],
  bootstrap: [AppComponent]
})
export class AppModule {

  constructor() {

    environment.isDesktop = !this.isMobile();

    let isApp: Boolean = localStorage.getItem("IsApp") === "yes";
    if (isApp) {
      environment.isMobileApp = true;
    }
  }


  isMobile(): boolean {
    if (navigator.userAgent.match(/Android/i)
      || navigator.userAgent.match(/webOS/i)
      || navigator.userAgent.match(/iPhone/i)
      || navigator.userAgent.match(/iPod/i)
      || navigator.userAgent.match(/BlackBerry/i)
      || navigator.userAgent.match(/Windows Phone/i)
    ) {
      return true;
    }
    else {
      return false;
    }
  }
}

