import { Component, OnInit, Injector, OnDestroy, Inject, Input } from '@angular/core';
import { FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, RouterEvent } from '@angular/router';
import { first } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { BaseComponent } from 'src/app/shared/base.component';
import { AuthenticationService } from '../_services/authentication.service';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss']
})
export class LoginComponent extends BaseComponent implements OnInit {

    submitted: boolean = true;

    modalId: string = "login-modal";

    assetUrl: string = environment.assetsUrl;

    showPassword: boolean = false;

    formInstance: FormGroup = this.fb.group({
        UserName: ['', [Validators.required]],
        Password: ['']
    })

    constructor(protected injector: Injector,
        private authenticationService: AuthenticationService) {
        super(injector);
    }

    get email() {
        return this.formInstance.get('UserName');
    }

    get password() {
        return this.formInstance.get('Password');
    }

    visibility() {
        this.showPassword = !this.showPassword;
    }


    ngOnInit(): void {
        this.showModal(this.modalId, () => {
            this.modalClose();
        });
    }

    modalClose() {
        if (this.router != null) {
            let outletUrls = [];
            if (this.router.url.indexOf('otp') >= 0) {
                outletUrls.push('otp');
            }
            this.router.navigate([{ outlets: { action: outletUrls, primary: [] } }])
        }
    }

    submitForm() {

        this.submitted = true;

        if (this.formInstance.invalid) {
            return;
        }

        this.showFullSpinner();
        let pass = this.password?.value == null ? '' : this.password?.value;
        this.authenticationService.login(this.email?.value, pass)
            .pipe(first())
            .subscribe(
                (user) => {
                    this.formInstance.reset();
                    if (user.requireOTP == "True") {
                        this.router.navigate([{ outlets: { action: ["otp"], primary: [] } }]);
                    } else {

                    }
                    this.hideFullSpinner();
                },
                (error: any) => {
                    setTimeout(() => {
                        this.hideFullSpinner();
                    }, 500);
                });
    }
}
