import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { environment } from 'src/environments/environment';
import { LoginComponent } from './auth/login/login.component';
import { LogoutComponent } from './auth/logout/logout.component';
import { RoleGuardService } from './auth/_helpers/role.guard';
import { Role } from './auth/_models/role';


const routes: Routes = [
    {
      path: '',
      loadChildren: () => environment.isDesktop
        ? import('./desktop/desktop.module').then(m => m.DesktopModule) :
        import('./mobile/mobile.module').then(m => m.MobileModule)
    },
    {
      path: 'logout', component: LogoutComponent, canActivate: [RoleGuardService],
      data: { roles: [Role.User] }
    }
  ];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    onSameUrlNavigation: 'reload'
  })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
